.text-accentuum {
  color: #3e9cdc;
}

.button {
  background-color: #ffffff;
  color: #2a2b30;
  text-decoration: none;
}

.button--dark {
  background-color: #3e9cdc;
  color: #ffffff;
}

.body {
  background-color: #3d4050;
  color: #ffffff;
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
  font-size: normal;
  font-weight: 400;
}

.header {
  background-color: rgba(16, 16, 29, 0.9);
  color: #ffffff;
  padding: 1.83rem 1rem 1.78rem;
  display: flex;
  flex-direction: "row";
  justify-content: space-between;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: "row";
  flex-wrap: wrap;
  gap: 0.83rem 3.33rem;
}

.navigation__link {
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.72rem;
  line-height: 123%;
}

.navigation__link--current {
  color: #43abf0;
}